@import 'variables';

.registerform{
    &__title{
        text-align: center;
        margin-top: $spacing-mobile;
        font-size: 2.2rem;
        font-family: $title-font;
    }

    &__input, &__label{
        display: block;
        font-family: $form-font;
        margin: auto;
        font-size: 1.3rem;
    }

    &__label{
        margin-top: $spacing-mobile;
        font-weight: bold;
        width: 90%;
        margin-left: 5%;
        color: gray;

        span{
            color: red;
            font-size: 1rem;
            vertical-align: top;
        }
    }

    &__input{
        padding: 8px 10px;
        border: 1px solid gray;
        border-radius: 7px;
        width: 100%;
        margin-top: 5px;
    }

    &__error{
        color: red;
    }
}