@import 'variables';
@import 'mixins';

.navbar{
    position: fixed;
    top: 0;
    bottom: 0;
    width: 100%;
    right: -100%;
    background-color: $menu-color-mobile;
    color: white;
    transition: .5s;
    z-index: 1;
    font-family: $menu-font;

    @media (min-width: 768px){
        width: 50%;
    }

    @media (min-width: 1024px){
        width: 100%;
        right: 0;
        height: 10vh;
    }

    &--active{
        right: 0;
    }

    &__mobile-icons{
        position: fixed;
        top: 25px;
        right: 25px;
        font-size: 2.3rem;
        z-index: 1;

        @media (min-width: 1024px){
           display: none;
        }

        & .no-active{
            display: none;
        }
    }

    &__logo-container{
        top: 0px;
        left: 0px;
        position: fixed;
        background-color: $menu-color-mobile;
        width: 100%;
        z-index: 0;

        @media (min-width: 1024px){
            position: absolute;
            height: 10vh;
            z-index: -1;
        }

        img{
            margin-left: 15px;
            width: 8rem;

            @media (min-width: 1024px){
                margin-left: 2%;
                height: 100%;
            }
        }
    }

    &__options-container{
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        margin-top: 8rem;
        height: calc(100vh - 8rem);
        text-transform: uppercase;

        @media (min-width: 1024px){
            flex-direction: row;
            margin-top: 0;
            margin-left: 14rem;
        }

        @media (min-width: 1440px){
            margin-left: 18rem;
        }

        .navbar__options-main{
            margin-top: $spacing-mobile;

            @media (min-width: 1024px){
                display: flex;
                margin: 0;
            }

            .navbar__option{
                font-size: 1.5rem;
                padding-top: $spacing-mobile / 2;
                padding-bottom: $spacing-mobile / 2;
                margin: 0 15px;
                cursor: pointer;
                border-bottom: 1px solid white;

                @media (min-width: 1024px){
                    line-height: 10vh;
                    padding-top: 0;
                    padding-bottom: 0;
                    padding-left: $spacing-desktop;
                    border-bottom: none;
                }

                p{
                    display: flex;
                    justify-content: space-between;

                    @media (min-width: 1024px){
                        display: block;

                        &:hover{
                            text-decoration: underline;
                        }
                    }
                }
            }
        }

        .navbar__options-login-register{
            margin-bottom: $spacing-mobile * 5;
            align-self: center;

            @media (min-width: 1024px){
                margin-bottom: 0;
                align-self: flex-start;
                margin-right: $spacing-desktop * 2;
            }

            .navbar__login, .navbar__register{
                color: white;
                margin: 0 15px;
                font-size: 1.8rem;
                text-decoration: none;

                @media (min-width: 1024px){
                    line-height: 10vh;
                }
            }

            .navbar__login{
                @media(min-width: 1024px){
                    &:hover{
                        text-decoration: underline;
                    }
                }
            }

            .navbar__register{
                border: 2px solid white;
                padding: 5px 10px;

                @media(min-width: 1024px){
                    &:hover{
                        background-color: white;
                        color: black;
                    }
                }
            }
        }
    }
}